import React from "react"
import { Link } from "gatsby"

const Book = ({ book }) => {
  return (
    <li key={book.title} className="book-item">
      <section className="image">
        <img src={book.image} alt={book.title} />
      </section>
      <section>
        <h5>{book.title}</h5>
        <span>
          De{" "}
          <Link to={book.author.link} itemProp="url">
            {book.author.name}
          </Link>
        </span>
        <div>
          <span>Lecturas:</span>
          <ul>
            {book.readings.map(r => {
              const isReading = r => !r.end_date
              const isFinished = r => r.end_date && r.end_date !== ""

              return (
                <li key={r.start_date}>
                  {isReading && !isFinished && (
                    <span>
                      Empezado el <time>{r.start_date}</time>
                    </span>
                  )}
                  {isFinished && (
                    <span>
                      Del <time>{r.start_date}</time> al{" "}
                      <time>{r.end_date}</time>
                    </span>
                  )}
                </li>
              )
            })}
          </ul>
        </div>
      </section>
    </li>
  )
}

export default Book
