import React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import Pagination from "../components/pagination"
import Recipe from "../components/recipe"
import BackButton from "../components/back-button"

const RecipeListTemplate = ({ data, location, pageContext }) => {
  const { numPages, currentPage, title } = pageContext
  const recipes = data.allMarkdownRemark.nodes

  return (
    <Layout location={location} title={title}>
      <SEO title={title} />
      <h1>{title}</h1>

      <BackButton location={location} path="/wiki/cooking" title="Recetas" />

      <ul className="recipe-list">
        {recipes.map(r => (
          <Recipe
            key={r.frontmatter.title}
            recipe={r}
            location={location}
            containerTitle={title}
          />
        ))}
      </ul>

      {numPages > 1 && (
        <Pagination
          location={location}
          pagination={{ numPages, currentPage, fields: 3 }}
        />
      )}
    </Layout>
  )
}

export default RecipeListTemplate

export const recipeListQuery = graphql`
  query recipeListQuery($skip: Int!, $limit: Int!, $regex: String!) {
    allMarkdownRemark(
      sort: { fields: [frontmatter___title], order: ASC }
      filter: {
        frontmatter: { draft: { ne: true } }
        fileAbsolutePath: { regex: $regex }
      }
      limit: $limit
      skip: $skip
    ) {
      nodes {
        fields {
          slug
        }
        frontmatter {
          title
          author
          image
          portion
          time
        }
      }
    }
  }
`
